import React, {Component} from 'react';
import {withStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
        overflowX: 'auto',
    },
    table: {
        minWidth: 700,
    },
});

class CustomTable extends Component {
    render() {
        const {classes, headers, rows} = this.props;
        return <div>
            <Paper className={classes.root}>
                <Table className={classes.table}>
                    <TableHead>
                        <TableRow>
                            {
                                headers.map(header => (<TableCell key={header.key}>{header.name}</TableCell>))
                            }
                        </TableRow>
                    </TableHead>

                    <TableBody>
                        {
                            rows.map((row, index) =>
                                <TableRow key={index}>{
                                    headers.map(header => {
                                            let headerKey = header.key;
                                            return <TableCell key={`${index}_${headerKey}`}>{row.find(r => r.key === headerKey).value} </TableCell>
                                        }
                                    )
                                }
                                </TableRow>
                            )
                        }
                    </TableBody>
                </Table>
            </Paper>
        </div>
    }
}

export default withStyles(styles)(CustomTable);